<template>
    <v-layout>
        <v-container>
            <h1>Calendar</h1>
            <v-alert border="left" color="red lighten-2" dark>We may be able to sync this with a shared calendar (Google, Office365, etc)</v-alert>
            <v-sheet height="64">
                <v-toolbar flat color="white">
                    <v-btn outlined class="mr-4" @click="setToday">Today</v-btn>
                    <v-btn fab text small @click="prev"><v-icon small>mdi-chevron-left</v-icon></v-btn>
                    <v-btn fab text small @click="next"><v-icon small>mdi-chevron-right</v-icon></v-btn>
                    <v-toolbar-title>{{ title }}</v-toolbar-title>
                    <div class="flex-grow-1"></div>
                    <!-- <v-menu bottom right>
                        <template v-slot:activator="{ on }">
                            <v-btn outlined v-on="on">
                                <span>{{ typeToLabel[type] }}</span>
                                <v-icon right>mdi-menu-down</v-icon>
                            </v-btn>
                        </template>
                        <v-list>
                            <v-list-item @click="type='day'">
                                <v-list-item-title>Day</v-list-item-title>
                            </v-list-item>
                            <v-list-item @click="type='week'">
                                <v-list-item-title>Week</v-list-item-title>
                            </v-list-item>
                            <v-list-item @click="type='month'">
                                <v-list-item-title>Month</v-list-item-title>
                            </v-list-item>
                            <v-list-item @click="ype='4day'">
                                <v-list-item-title>4 days</v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </v-menu> -->
                </v-toolbar>
            </v-sheet>
            <v-sheet height="600">
                <v-calendar
                    ref="calendar"
                    v-model="focus"
                    color="primary"
                    :type="type"
                    :events="events"
                    :event-color="getEventColor"
                    @change="updateRange"
                />
            </v-sheet>
        </v-container>
    </v-layout>
</template>

<script>
    import axios from "axios"

    export default {
        data: () => ({
            today: new Date().toJSON(),
            focus: new Date().toJSON(),
            type: "month",
            start: null,
            end: null,
            typeToLabel: {
                month: "Month",
                week: "Week",
                day: "Day",
                "4day": "4 Days"
            },
            events: []
            //     {
            //         name: "Car Wash",
            //         start: "2019-08-27 17:00",
            //         end: "2019-08-27 19:00"
            //     },
            //     {
            //         name: "Moon Festival",
            //         start: "2019-09-13 16:00",
            //         end: "2019-09-13 21:00"
            //     },
            //     {
            //         name: "Planning Meeting",
            //         start: "2019-09-10 17:00",
            //         end: "2019-09-10 18:00",
            //         color: "red lighten-2"
            //     }
            // ]
        }),
        computed: {
            title () {
                const { start, end } = this;
                if (!start || !end) {
                    return "";
                }

                const startMonth = this.monthFormatter(start);
                const endMonth = this.monthFormatter(end);
                const suffixMonth = startMonth === endMonth ? "" : endMonth;

                const startYear = start.year;
                const endYear = end.year;
                const suffixYear = startYear === endYear ? "" : endYear;

                const startDay = start.day + this.nth(start.day);
                const endDay = end.day + this.nth(end.day);

                switch (this.type) {
                    case "month":
                        return `${startMonth} ${startYear}`;
                    case "week":
                    case "4day":
                        return `${startMonth} ${startDay} ${startYear} - ${suffixMonth} ${endDay} ${suffixYear}`;
                    case "day":
                        return `${startMonth} ${startDay} ${startYear}`;
                }

                return "";
            },
            monthFormatter() {
                return this.$refs.calendar.getFormatter({
                    timeZone: "UTC", month: "long"
                })
            }
        },
        methods: {
            setToday() {
                this.focus = this.today;
            },
            prev() {
                this.$refs.calendar.prev();
            },
            next() {
                this.$refs.calendar.next();
            },
            nth(d) {
                return d > 3 && d < 21 ? "th" : ["th", "st", "nd", "rd", "th", "th", "th", "th", "th", "th"][d % 10];
            },
            updateRange({ start, end }) {
                this.start = start;
                this.end = end;
            },
            getEventColor(event) {
                return event.color || "blue lighten-1";
            }
        },
        created() {
            axios.get("https://www.googleapis.com/calendar/v3/calendars/djgh8mb8d0rh95nvqn0thov608@group.calendar.google.com/events?key=AIzaSyAxumYfeCkzW0tokGxNKJES09Nz3Zyp84c")
                .then(response => {
                    this.events.push.apply(this.events, response.data.items.map(d => {
                        let evt = {
                            name: d.summary,
                            start: d.start.dateTime ? d.start.dateTime.replace(/(\d+)-(\d+)-(\d+)T(\d+):(\d+):(\d+)-\d+:\d+/, "$1-$2-$3 $4:$5") : d.start.date,
                            end: d.end.dateTime ? d.end.dateTime.replace(/(\d+)-(\d+)-(\d+)T(\d+):(\d+):(\d+)-\d+:\d+/, "$1-$2-$3 $4:$5") : d.start.date
                        };

                        return evt;
                    }));
                });
        }
    }
</script>

<style lang="scss" scoped>

</style>